.product img {
    width: 300px;
  }
  
  .product {
    margin: 20px;
    border: 1px solid #eee;
    padding: 20px;
    width: 300px;
    display: flex;
    align-items: center;
    justify-content: center;
    
    flex-direction: column; 
  }
  
  button {
    width: 100px;
    background: black;
    color: white;
    padding: 10px;
    border-radius: 5px;
    cursor: pointer;
    border-radius: 15px;
  }